.footer {
  background-color: $pink-bcg-light;
}

.container {
  &__change {
    margin-bottom: 43px;

    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: space-between;
      margin-bottom: 91px;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: unset;
    }
  }
}

.footer-texts {
  max-width: 234px;
  margin-bottom: 43px;

  @media screen and (min-width: 768px) {
    max-width: 340px;
    margin-bottom: unset;
  }

  @media screen and (min-width: 1280px) {
    max-width: 477px;
    margin-bottom: 97px;
  }

  &__title {
    max-width: 232px;
    font-family: $font-family-primary;
    font-size: 18px;
    line-height: 1.17;
    text-transform: uppercase;
    color: $pink-font;

    @media screen and (min-width: 768px) {
      max-width: 340px;
      font-size: 26px;
      line-height: 1.15;
      letter-spacing: 0.04em;
      margin-top: auto;
      margin-bottom: 16px;
    }

    @media screen and (min-width: 1280px) {
      max-width: 472px;
      font-size: 30px;
      line-height: 1.13;
      margin-bottom: 15px;
    }
  }

  &__catchword {
    max-width: 200px;
    font-family: $font-family-primary;
    font-size: 34px;
    line-height: 1.15;
    text-transform: uppercase;
    color: $red-font;

    @media screen and (min-width: 768px) {
      max-width: 340px;
      font-size: 48px;
      letter-spacing: 0.06em;
      margin-top: auto;
      margin-bottom: auto;
    }

    @media screen and (min-width: 1280px) {
      max-width: 477px;
      font-size: 58px;
      line-height: 1.14;
    }
  }
}

