.footer-social__list {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 46px;
  gap: 28px;

  @media screen and (min-width: 768px) {
    gap: 30px;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 50px;
    gap: 24px;
  }
}

.footer-social__sprite {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  fill: $white;
  background-color: $pink-svg;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    width: 34px;
    height: 34px;
  }

  &:hover,
  &:focus {
    background-color: $red-font;
    fill: $pistachio;
  }
}

.footer-social__icon {
  width: 18px;
  height: 19px;

  @media screen and (min-width: 768px) {
    width: 20px;
    height: 20px;
  }
}

.footer-contact {
  display: flex;
  justify-content: flex-end;
  font-weight: 700;
  font-family: $font-family-secondary;
  font-size: 12px;
  line-height: 1.83;
  margin-bottom: 43px;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.86;
    margin-bottom: unset;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.88;
    letter-spacing: 0.02em;
  }

  &__link {
    color: $red-font;
    text-decoration: none;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
      color: $pink-font;
    }
  }
}

.footer-copyright {
  border-top: 1px solid #e1e1e1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 22px;
  padding-bottom: 25px;

  @media screen and (min-width: 768px) {
    padding-top: 29px;
    padding-bottom: 18px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 9px;
    padding-bottom: 10px;
  }

  &__info {
    max-width: 150px;
    font-family: $font-family-secondary;
    text-align: center;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    color: #907e82; //wstawić zmienną!!!!!!!!!!
    padding: 0 5px;

    @media screen and (min-width: 768px) {
      max-width: 276px;
      line-height: 1.86;
      letter-spacing: 0.02em;
      padding: unset;
    }
  }
}
